*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: 18.2px;
  color: #212121;
  background-color: #F4F4F4;
  margin: 0;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

button {
  border: none; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.banner {
  padding: 0;
  margin: 0; }
  .banner__header {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .banner__pill-name {
    font-weight: bold;
    font-size: 18px;
    line-height: 130%; }
  .banner__pill-info {
    font-weight: bold;
    font-size: 18px;
    line-height: 130%;
    text-transform: uppercase; }
  .banner__logo {
    position: relative;
    margin-top: 9px; }
  .banner__logo-bg {
    width: 202px;
    height: 202px;
    background: #428A60;
    border-radius: 50%;
    margin: 0 auto; }
  .banner__logo-image {
    position: absolute;
    top: 48px;
    left: 60%;
    transform: translateX(-59%); }

@media (min-width: 768px) {
  .banner__logo-image {
    position: absolute;
    top: 48px;
    left: 55%;
    transform: translateX(-59%); } }

@media (min-width: 1280px) {
  .banner {
    width: 560px;
    height: 500px;
    background: linear-gradient(180deg, #428A60 0%, #35734F 100%);
    border-radius: 1px; }
    .banner__logo {
      margin-top: 0; }
    .banner__logo-bg {
      position: relative;
      width: 398px;
      height: 398px;
      top: 70px;
      left: -60px; }
    .banner__logo-image {
      top: 80px;
      left: 59%;
      transform: translateX(-59%); }
    .banner__header {
      position: absolute;
      flex-direction: column;
      margin: 40px 40px 0 40px;
      color: #fff;
      height: 425px; } }

.popup {
  padding-bottom: 28px;
  margin: 60px 0 0 0;
  background-color: #fff;
  box-shadow: 0px 24px 80px rgba(0, 0, 0, 0.1); }
  .popup__wrapper {
    display: block;
    padding: 40px 20px 0 20px; }
  .popup__main {
    margin-top: 37px; }
  .popup__header {
    font-weight: 800;
    font-size: 30px;
    line-height: 130%;
    text-align: center;
    word-wrap: break-word; }
  .popup__button-wrapper {
    margin-top: 9px; }
  .popup__button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: #428A60;
    border-radius: 5px;
    width: 280px;
    height: 42px;
    font-weight: 600;
    line-height: 130%;
    color: #fff;
    font-size: 14px;
    padding: 12px 20px;
    cursor: pointer; }
    .popup__button:hover {
      background: #52a876; }
    .popup__button:active {
      background: #286842; }
  .popup__button-arrow {
    width: 20px;
    padding: 0;
    margin-left: 7px; }

@media (min-width: 768px) {
  .popup__wrapper {
    padding: 0 50px; } }

@media (min-width: 1280px) {
  .popup {
    width: 1120px;
    padding: 0;
    margin: 75px auto; }
    .popup__wrapper {
      display: flex;
      flex-direction: row;
      padding: 0; }
    .popup__main {
      width: 560px;
      background-color: #F8F8F8;
      border-top-right-radius: 24px;
      padding: 0;
      margin: 0; }
    .popup__header {
      padding: 29px 128px 29px 129px; }
    .popup__button-wrapper {
      margin-top: 19px; }
    .popup__button {
      width: 100%; } }

.form {
  margin-top: 28px;
  padding: 0; }
  .form__item {
    display: flex;
    flex-direction: column; }
  .form__name {
    color: #428A60; }
  .form__item label {
    font-weight: 800;
    font-size: 12px;
    line-height: 130%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin-bottom: 10px; }
  .form input[type=text],
  .form input[type=tel] {
    width: 100%;
    height: 50px;
    background: #F8F8F8;
    border: 1px solid #E4E4E4;
    border-radius: 5px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 130%;
    margin-bottom: 20px;
    padding: 16px 10px; }
    .form input[type=text]:focus,
    .form input[type=tel]:focus {
      border: 1px solid #444444; }
    .form input[type=text]:focus:valid,
    .form input[type=tel]:focus:valid {
      border: 1px solid #428A60; }
    .form input[type=text]:focus:invalid,
    .form input[type=tel]:focus:invalid {
      border: 1px solid #ff8182; }
  .form ::-webkit-input-placeholder {
    opacity: 0.2; }
  .form :-moz-placeholder {
    opacity: 0.2; }
  .form ::-moz-placeholder {
    opacity: 0.2; }
  .form :-ms-input-placeholder {
    opacity: 0.2; }
  .form__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center; }
  .form__arrow {
    width: 10px;
    height: 8px; }
  .form__dropdowm-item {
    align-items: center;
    background: #F8F8F8;
    border: 1px solid #E4E4E4;
    border-radius: 5px;
    width: 96.18px;
    height: 50px;
    margin-left: 10px;
    list-style: none;
    margin-top: 5px;
    padding: 16px 10px;
    cursor: pointer; }
  .form__dropdowm-item-descr {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 130%;
    text-transform: uppercase; }
  .form__dropdown-item a {
    color: black;
    padding: 12px 16px;
