    display: block; }

@media (min-width: 1280px) {
  .form {
    margin: 52px 0 60px 0;
    padding: 0 123px 0 117px; }
    .form input[type=text],
    .form input[type=tel] {
      padding-left: 20px;
      padding-right: 25px; }
    .form__wrapper .form__item:nth-child(1) {
      width: 184px;
      flex: 1; }
    .form__wrapper .form__item:nth-child(2) {
