    .form__dropdowm-item {
      width: 116.18px;
      margin-left: 20px;
      padding: 16px 20px 10px 20px; } }

.mobile-footer {
  background-color: #F8F8F8;
  padding: 0;
  margin-bottom: 59px; }
  .mobile-footer__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 11px 80px; }

@media (min-width: 768px) {
